import * as React from 'react';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import { useTheme } from '@mui/material/styles';
import Drawer from '@mui/material/Drawer';
import Toolbar from '@mui/material/Toolbar';
import CssBaseline from '@mui/material/CssBaseline';
import List from '@mui/material/List';
import Divider from '@mui/material/Divider';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft';
import ChevronRightIcon from '@mui/icons-material/ChevronRight';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import InboxIcon from '@mui/icons-material/MoveToInbox';
import MailIcon from '@mui/icons-material/Mail';
import OutboxIcon from '@mui/icons-material/Outbox';
import MouseIcon from '@mui/icons-material/Mouse';

import { styled } from '@mui/material/styles';
import MuiAppBar from '@mui/material/AppBar';

const drawerWidth = 240;

const Main = styled('main', { shouldForwardProp: (prop) => prop !== 'open' })(
  ({ theme, open }) => ({
    flexGrow: 1,
    padding: theme.spacing(3),
    transition: theme.transitions.create('margin', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    marginRight: -drawerWidth,
    ...(open && {
      transition: theme.transitions.create('margin', {
        easing: theme.transitions.easing.easeOut,
        duration: theme.transitions.duration.enteringScreen,
      }),
      marginRight: 0,
    }),
  }),
);

const AppBar = styled(MuiAppBar, {
  shouldForwardProp: (prop) => prop !== 'open',
})(({ theme, open }) => ({
  transition: theme.transitions.create(['margin', 'width'], {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.leavingScreen,
  }),
  ...(open && {
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(['margin', 'width'], {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
    marginRight: drawerWidth,
  }),
}));

const DrawerHeader = styled('div')(({ theme }) => ({
  display: 'flex',
  alignItems: 'center',
  padding: theme.spacing(0, 1),
  // necessary for content to be below app bar
  ...theme.mixins.toolbar,
  justifyContent: 'flex-start',
}));


export const PersistentDrawerRight = () => {
  const theme = useTheme();
  const [open, setOpen] = React.useState(false);

  const handleDrawerOpen = () => {
    setOpen(true);
  };

  const handleDrawerClose = () => {
    setOpen(false);
  };

  return (
    <Box sx={ { display: 'flex' } }>
      <CssBaseline />
      <AppBar position="fixed" open={ open }>
        <Toolbar>
          <Typography variant="h6" noWrap sx={ { flexGrow: 1 } } component="div">
            Logo here
          </Typography>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            edge="end"
            onClick={ handleDrawerOpen }
            sx={ { ...(open && { display: 'none' }) } }
          >
            <MenuIcon />
          </IconButton>
        </Toolbar>
      </AppBar>
      <Main open={ open }>
        <DrawerHeader />
      {/*  */}

      </Main>
      <Drawer
        sx={ {
          width: drawerWidth,
          flexShrink: 0,
          '& .MuiDrawer-paper': {
            width: drawerWidth,
          },
        } }
        variant="persistent"
        anchor="right"
        open={ open }
      >
        <DrawerHeader>
          <IconButton onClick={ handleDrawerClose }>
            { theme.direction === 'rtl' ? <ChevronLeftIcon /> : <ChevronRightIcon /> }
          </IconButton>
        </DrawerHeader>
        <Divider />
        <List>
          { ['Our Services content', 'Our Work content', 'Stay Relevant content', 'About content', 'Careers content', 'Investors content'].map((text, index) => (
            <ListItem key={ text } disablePadding>
              <ListItemButton>
                <ListItemIcon>
                  { index % 2 === 0 ? <MouseIcon /> : <MouseIcon /> }
                </ListItemIcon>
                <ListItemText primary={ text } />
              </ListItemButton>
            </ListItem>
          )) }
        </List>
        <Divider />
        <List>
          { ['Contact us'].map((text, index) => (
            <ListItem key={ text } disablePadding>
              <ListItemButton>
                <ListItemIcon>
                  { index % 2 === 0 ? <OutboxIcon /> : <OutboxIcon /> }
                </ListItemIcon>
                <ListItemText primary={ text } />
              </ListItemButton>
            </ListItem>
          )) }
        </List>
      </Drawer>
    </Box>
  );
}
